<template>
  <el-container>
    <el-aside width="220px" class="class0">
      <el-scrollbar style="height: 100%">
        <el-menu
          :default-openeds="['1', '2']"
          text-color="#fff"
          active-text-color="#ffd04b"
          style="top: 79px"
        >
          <a href="/Member_management">
            <el-menu-item>
              <img src="imgs/.png" class="class0_a" />
              <span slot="title">  </span>
            </el-menu-item>
          </a>

          <el-submenu index="1">
            <template slot="title">
              <img src="imgs/.png" class="class0_a" />
              <span style="color: white">  </span>
            </template>
            <el-menu-item-group>
              <a href="/Achievement">
                <el-menu-item index="2-1" style="font-size: 16px">
                  
                </el-menu-item>
              </a>
              <a href="/Achievement_analysis">
                <el-menu-item index="2-2" style="font-size: 16px">
                  
                </el-menu-item>
              </a>
            </el-menu-item-group>
          </el-submenu>
        </el-menu>
      </el-scrollbar>
    </el-aside>

    <el-container>
      <el-head class="class1">
        <a
          href="/Member_management"
          class="class4"
          style="text-decoration: none"
        >
          &lt; 
        </a>
      </el-head>

      <el-main class="class2">
        <el-row>
          <el-col :span="3">
            <div class="class3"></div>
          </el-col>
        </el-row>
        <el-divider class="class6"></el-divider>
        <el-form ref="form" :model="form" label-width="80px" class="class5">
          <el-form-item label="" style="text-align: left; width: 443px">
            <el-input
              v-model="form.name"
              placeholder=""
            ></el-input>
          </el-form-item>
          <el-form-item label="" style="text-align: left; width: 443px">
            <el-input
              v-model="form.orgName"
              placeholder=""
            ></el-input>
          </el-form-item>
          <el-form-item label="" style="text-align: left; width: 443px">
            <el-input v-model="form.id" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" style="text-align: left; width: 443px">
            <el-input
              v-model="form.email"
              placeholder=""
            ></el-input>
          </el-form-item>
          <el-form-item label="" style="text-align: left">
            <el-select
              v-model="form.title"
              placeholder=""
              style="width: 363px"
            >
              <el-option label="" value="yuanshi"></el-option>
              <el-option label="" value="jiaoshou"></el-option>
              <el-option label="" value="fujiaoshou"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="get_staff"></el-button>
          </el-form-item>
        </el-form>

        <el-row v-if="find == true">
          <el-col :span="3">
            <div class="class15"></div>
          </el-col>
          <el-divider class="class16"></el-divider>
        </el-row>

        <span
          class="class17"
          v-for="(card, index) in cards"
          :key="index"
          :style="{ top: 650 + Math.floor(index / 3) * 400 + 'px' }"
          :class="setClass(index)"
        >
          <el-row>
            <!-- :srcv-bind -->
            <img class="class18" :src="card.avg" />

            <el-col :span="6">
              <div class="class19">
                {{ card.name }}
              </div>
              <div class="class20">
                {{ card.orgName }}
              </div>
            </el-col>
            <el-col :span="6">
              <el-button type="primary" class="class21">
                <i class="el-icon-plus class21_a"></i>
              </el-button>
            </el-col>
          </el-row>

          <el-row>
            <el-col :span="2" class="class22">
              <img src="/imgs/ 2/.png" />
            </el-col>
            <el-col :span="5" class="class23"></el-col>
          </el-row>

          <el-row>
            <el-col :span="10">
              <div class="class25">
                {{ card.paperTitle }}
              </div>
            </el-col>
          </el-row>

          <el-row>
            <el-col :span="4">
              <div class="class26">
                {{ card.paperCount }}
              </div>
              <div class="class27"></div>
            </el-col>

            <el-col :span="4">
              <div class="class28">
                {{ card.patentCount }}
              </div>
              <div class="class29"></div>
            </el-col>

            <el-col :span="4">
              <div class="class30">
                {{ card.projectCount }}
              </div>
              <div class="class31"></div>
            </el-col>
          </el-row>
        </span>
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
export default {
  name: "Member_add",
  data() {
    return {
      form: {
        name: "",
        orgName: "",
        id: "",
        email: "",
        title: "",
      },
      cards: [],
      img: "",
      find: false,
    };
  },
  methods: {
    onSubmit() {
      console.log("");
    },
    setClass(num) {
      return num % 3 == 0
        ? "class17_1"
        : num % 3 == 1
        ? "class17_2"
        : "class17_3";
    },
    get_staff() {
      var url = "/v1/scholars/getScholarByName/?scholarName=";
      url = url + this.form.name + "&orgName=" + this.form.orgName;
      this.$axios
        .get(url)
        .then((response) => {
          this.cards = response.data.data.content;
          console.log(response);
        })
        .catch((error) => {
          console.log("");
          console.log(error);
        });
      this.find = true;
    },
  },
  mounted() {
    document
      .querySelector("body")
      .setAttribute("style", "background-color:white");
  },
  beforeDestroy() {
    document.querySelector("body").removeAttribute("style");
  },
};
</script>

