<template>
  <el-container>
    <el-main>
      <div>
        <span class="class137">
          <img style="size: 100%" src="imgs/-/.png" />
        </span>
        <div class="class138">xx</div>
        <div class="class139"></div>
        <el-form
          ref="ref"
          class="class141"
          :model="form"
          :rules="LoginformRules"
        >
          <el-form-item>
            <img src="imgs/-/.png" class="class141_a" />
            <el-input
              v-model="form.tel"
              prop="tel"
              placeholder=""
              type="telephone"
              class="class141_b"
            ></el-input>
          </el-form-item>
          <el-divider class="class148_a"></el-divider>
          <el-form-item>
            <img src="imgs/-/.png" class="class141_c" />
            <el-input
              type="password"
              prop="password"
              v-model="form.password"
              placeholder=""
              class="class141_d"
            >
            </el-input>
          </el-form-item>
          <el-divider class="class148_b"></el-divider>
          <el-form-item>
            <el-checkbox v-model="form.remember" class="class141_e"
              ></el-checkbox
            >
          </el-form-item>
          <el-form-item>
            <el-button type="primary" class="class142" v-on:click="do_Login()"
              ></el-button
            >
          </el-form-item>
        </el-form>
      </div>
    </el-main>
  </el-container>
</template>
<script>
export default {
  name: "Sign",
  data() {
    return {
      form: {
        tel: "",
        password: "",
        remember: "",
      },
      LoginformRules: {
        tel: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 10,
            max: 10,
            message: "10",
            trigger: "blur",
          },
        ],
        password: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 6,
            max: 15,
            message: " 6  15 ",
            trigger: "blur",
          },
        ],
        persons: {},
      },
    };
  },
  methods: {
    do_Login() {
      var url = "/v1/scholars/getScholarByName/?scholarName=";
      url = url + "" + "&orgName=" + "";
      this.$axios
        .get(url)
        .then((response) => {
          this.persons = response.data.data.content[0];
          console.log(response);
          console.log(this.persons);
          // 
          this.$router.push({
            name: "Achievement_acknowlege",
            // query: {
            //   persons: this.persons,
            // },
          });
        })
        .catch((error) => {
          console.log("");
          console.log(error);
        });
    },
  },
  mounted() {
    document
      .querySelector("body")
      .setAttribute("style", "background-color:white");
  },
  beforeDestroy() {
    document.querySelector("body").removeAttribute("style");
  },
};
</script>

